.main {
  display: grid;
margin-bottom: 100px;
  h2 {
    font-family: "Montserrat", "Arial", sans-serif;
    font-size: 32px;
    font-weight: 700;
    line-height: 39px;
    text-transform: uppercase;
    text-align: center;
    color: $blue;
    margin: 0;
    margin-bottom: 24px;

    @media (min-width: $desktop-width) {
      font-size: 40px;
      line-height: 49px;
    }
  }

  p {
    margin: 0;
    font-size: 16px;
    font-weight: 400;
    line-height: 26px;
  }
}
