.header {
  position: relative;
  background-image: url("../img/header/girl.png"), url("../img/header/israel.png"), url("../img/header/bg.png");
  background-size: 320px 605px, 347px 122px, 912px 587px;
  background-position: center 32px, center 168px, center 32px;
  background-repeat: no-repeat;
  color: $black;

  @media (min-width: $desktop-width) {
    background-image: url("../img/header/girl-desktop.png"), url("../img/header/israel.png"), url("../img/header/bg.png");
    background-size: 1366px 906px, 1018px 340px, 1366px 879px;
    background-position: center 0, center 160px, center 0;

  }
}

.header__container {
  display: grid;
  row-gap: 269px;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 59px;

  @media (min-width: $desktop-width) {
    max-width: 1152px;
    row-gap: 458px;
    margin-bottom: 0;
  }
}

.header__top-row {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
  margin-left: 16px;
  margin-right: 7px;
  font-size: 14px;
  line-height: 17px;

  @media (min-width: $desktop-width) {
    position: relative;
    min-height: 48px;
    margin-top: 32px;
    margin-right: 5px;
    margin-left: 5px;

    font-size: 15px;
    line-height: 18px;
  }
}

.header__logo {
  margin-right: auto;

  @media (min-width: $desktop-width) {
    position: absolute;
    left: 44%;
    width: 137px;
    height: 48px;
  }
}

.header__contacts {
  display: flex;
  flex-direction: column;
  align-items: flex-end;

  @media (min-width: $desktop-width) {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
}

.header__scroll-button {
  position: relative;
  width: 54px;
  height: 54px;
  border-radius: 50%;
  border: 2px solid $white;

  &::after {
    position: absolute;
    top: 62px;
    content: "";
    width: 2px;
    height: 55px;
    background-color: $white;
  }
}

.header__scroll-button-round {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: auto;
  margin-left: auto;
  width: 76px;
  height: 76px;

  border-radius: 50%;
  border-width: 2px;
  border-image: url('../img/header/dashed-border.svg') 0 fill;
}

.header__phone {
  margin-bottom: 8px;
  font-weight: 500;

  color: $black;

  @media (min-width: $desktop-width) {
    margin-bottom: 0;
  }
}

.header__order-call {
  margin-right: 2px;
  font-weight: 500;
  font-variant: small-caps;
  font-size: 14px;
  line-height: 17px;
  border-bottom: 2px dashed $black;

  @media (min-width: $desktop-width) {
    font-size: 15px;
    line-height: 18px;
  }
}

.header__bottom-row {
  display: grid;
  row-gap: 32px;
  grid-template-columns: 100%;
  margin-right: 16px;
  margin-left: 16px;
  margin-bottom: 82px;

  h2 {
    width: 288px;
    margin: 0;
    font-size: 20px;
    font-weight: 700;
    line-height: 24px;
    text-align: center;
    color: $white;
    text-shadow: 0px 1px 4px rgba(0, 0, 0, 0.12);

    @media (min-width: $desktop-width) {
      width: 343px;
      margin-right: auto;
      margin-left: auto;
      font-size: 24px;
      line-height: 29px;
    }
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 202px;
  }
}
